.main-nav {
    width: 100%;
    height: 5rem;
    border: 1px solid black;
    display: grid;
    grid-template-columns: 10rem 1fr 2fr 1fr 10rem;
    box-shadow: rgba(50,50,93,0.25) 0px 50px 100px -20px,
                  rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
     background-color:#202124 ;  
              
}
.logo {
    display: grid;
    grid-column:2/3 ;
    justify-content: start;
    align-items: center;
    

}
.logo > img {
    width: 85%;
    height: 50%;
    margin-top: -25px;
}
.menu-link {
    grid-column: 3/4;
}
.menu-link ul {
    height: 5rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.menu-link ul li {
    font-size: 0.9rem;
    display: flex;
    justify-content: space-around;
    
}
.menu-link ul li a {
    transition: 0.5s;
    color: white;
    
}
.menu-link ul li:hover > a {
    transform-origin: left;
    text-decoration: none;
    transition: 0.5s;

}
.hamburger-menu {
    display: none;
}
@media (max-width:1080px) {
    .main-nav {
        height: 4rem;
        grid-template-columns: 1rem 3fr 3fr 1fr 1rem;
    }
    .logo,
    .menu-link ul {
        height: 4rem;
    }

}
@media (max-width: 998px) {
    .main-nav {
        height: 3.5rem;
        grid-template-columns: 1rem 2fr 3fr 1rem 1rem;
    }
    .menu-link {
        display: none;
    }
    .logo {
        height: 3.5rem;
    }
    .mobile-menu-link {
        grid-column: 2/4;
        position: relative;
        z-index: 99;
    }
    .mobile-menu-link {
        background-color: White;
        height: 20rem;
        display: grid;
        grid-column: 2/5;
        align-items: center;
        padding-left: 3rem;
        transition: all 2s linear;
        transform-origin: top;
        box-shadow: rgba(50,50,93,0.25) 0px 50px 100px -20px,
        rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;

    }
    .mobile-menu-link ul {
        height: 20rem;
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        align-items: start;
        
    }
    .mobile-menu-link ul li:first-child {
        transition-delay: 0.2s;
    }
    .hamburger-menu {
        display: block;
        font-size: 2rem;
        margin-left: 125px ;
    }
}